<template>
    <glui-table :data='modules' :show-header='false'>
        <glui-table-column prop="Name" width="100">
        </glui-table-column>
        <glui-table-column>
            <template slot-scope="scope">
                <ul v-if="scope.row.Module==6">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText">
                                
                                <glui-select v-model="item.MinValue" placeholder="">
                                    <glui-option :value="0" label="">
                                    </glui-option>
                                    <glui-option :value="1" label="">
                                    </glui-option>
                                    <glui-option :value="2" label="">
                                    </glui-option>
                                </glui-select>
                                
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==1">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText" v-if="item.Type==1&&belong==3">
                                
                                <glui-checkbox-group v-model="item.Values">
                                    <glui-checkbox label="1"></glui-checkbox>
                                    <glui-checkbox label="2"></glui-checkbox>
                                    <glui-checkbox label="3"></glui-checkbox>
                                    <glui-checkbox label="4"></glui-checkbox>
                                </glui-checkbox-group>
                            </div>
                            <div class="chkText" v-else-if="item.Type==1&&belong==2">
                                ()
                            </div>
                            <div class="chkText" v-else>
                                
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==2">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText" v-if="item.Type==9">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 
                                <font style="color:red;"></font>
                            </div>
                            <div class="chkText" v-else-if="item.Type==10">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 
                                <font style="color:red;"></font>
                            </div>
                            <div class="chkText" v-else-if="item.Type==11">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==12">
                                , 
                                <input type="number" class="input  input-small" v-model="item.MinValue"> ()
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> ()1
                                <input type="number" class="input  input-small" v-model="item.MinAge"> 
                                <input type="number" class="input  input-small" v-model="item.MaxAge"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==2">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==3">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==4">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==5">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==6">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> --
                                <input type="number" class="input  input-small" v-model="item.MaxValue"> 
                            </div>
                            <div class="chkText" v-else-if="item.Type==21">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue"> .
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==3">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div class="chkText" v-if="item.Type==13">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue" />
                            </div>
                            <div class="chkText" v-else-if="item.Type==14">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue" />
                            </div>
                            <div class="chkText" v-else-if="item.Type==15">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue" />
                            </div>
                            <div class="chkText" v-else-if="item.Type==16">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue" />
                            </div>
                            <div class="chkText" v-else-if="item.Type==7">
                                
                                <input type="number" v-model="item.MinValue" />
                            </div>
                            <div class="chkText" v-else-if="item.Type==8">
                                
                                <input type="number" class="input  input-small" v-model="item.MinValue" />
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else-if="scope.row.Module==4">
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div v-if="item.Type==17" class="chkText">
                                 
                                <glui-select v-model="item.IsAccept" placeholder="">
                                    <glui-option :value="0" label="">
                                    </glui-option>
                                    <glui-option :value="1" label="">
                                    </glui-option>
                                </glui-select>
                                <glui-select v-model="item.Values" multiple filterable remote :remote-method="queryCity" placeholder="">
                                    <glui-option :key="city.id" :value="city.id" :label="city.name" v-for="city in citySource">
                                    </glui-option>
                                </glui-select>
                                
                            </div>
                            <div v-else class="chkText">
                                 
                                <input type="text" class="input  input-medium" v-model="item.Residence" /> 
                            </div>
                        </div>
                    </li>
                </ul>
                <ul v-else>
                    <li :key="item.Type" v-for="item in scope.row.Items">
                        <div class="chkItem">
                            <glui-checkbox v-model="item.IsCheck">
                            </glui-checkbox>
                            <div style="display: inline-block;width: 97%;padding-left: 10px;">
                                <p :key="index" v-for="(val,index) in item.Values">
                                    {{val}}
                                </p>
                            </div>
                        </div>
                    </li>
                </ul>
            </template>
        </glui-table-column>
    </glui-table>

</template>
<script>
import httputils from "@/js/httputils";
export default {
    data: function () {
        return {
            belong: 1,
            modules: [],
            citySource: [],
            AgeLimitType: 0
        };
    },
    props: {
        productid: {
            type: [Number, String],
            default: 0
        },
        pname: {
            type: String,
            default: ""
        }
    },
    methods: {
        queryCity: function (query) {
            var that = this;
            if (!query || query.length <= 0) {
                return;
            }
            httputils.get('/travel/supergoproduct/Common/GetCity',
                {
                    key: query,
                    isonlydiji: true,
                    type: 1
                }).then(result => {
                    var lstCity = [];
                    if (result) {
                        result.forEach(item => {
                            lstCity.push({
                                id: item.id.toString(),
                                name: item.name
                            });
                        });
                        // that.$nextTick(() => {
                        //     cb && cb();
                        // });
                    }
                    that.citySource = lstCity;
                });
        },
        loadDatas() {
            var that = this;
            return new Promise((resolve, reject) => {
                httputils.get('/travel/supergoproduct/BookLimit/GetLimits', {
                    pid: that.productid
                }).then(resp => {
                    if (resp && resp.Result) {
                        that.belong = resp.Result.ProjectBelong
                        that.modules = resp.Result.Modules
                        that.modules && that.modules.forEach(module => {
                            if (module.Module === 4) {
                                module.Items && module.Items.forEach(item => {
                                    if (item.Type === 17) {
                                        let ids = item.Values;
                                        if (item.Values && item.Values.length >= 1) {
                                            that.queryCity(item.Values.join(","));
                                        }
                                    }
                                });
                            }
                        });
                        //if (that.belong == 2) {
                        //    $.each(that.modules, function (i, item) {
                        //        if (item.Module == 2) {
                        //            $.each(item.Items, function (i, k) {
                        //                if (k.IsCheck && (k.Type == 9 || k.Type == 10 || k.Type == 12 || k.Type == 22)) {
                        //                    that.AgeLimitType = k.Type;
                        //                }
                        //            });
                        //        }
                        //    });
                        //}

                        resolve()
                    } else {
                        reject(`${that.pname}:${resp.ErrorMsg}`)
                    }
                }).catch(err => {
                    console.log(err);
                    reject(`${that.pname}`)
                })
            });
        },
        saveData() {
            var that = this;
            //if (that.belong == 2) {
            //    $.each(that.modules, function (i, item) {
            //        if (item.Module == 2) {
            //            $.each(item.Items, function (i, k) {
            //                if (that.AgeLimitType == k.Type) {
            //                    k.IsCheck = true;
            //                }
            //            });
            //        }
            //    });
            //}
            return new Promise((resolve, reject) => {
                httputils.post("/travel/supergoproduct/BookLimit/SaveLimits",
                    {
                        request: that.modules,
                        pid: that.productid
                    }).then(res => {
                        resolve({ success: res && res.IsSuccess, msg: `${that.pname}:${res.ErrorMsg}` });
                    }).catch(err => {
                        resolve({ success: false, msg: `${that.pname}:${err}` });
                    });
            });
        }
    }
}
</script>

<style scoped>
</style>





